
.container-main-text {
  overflow-x: hidden;
  height: 100vh;
  background-image: linear-gradient(180deg, #072348 0%, #224DB7 100%);
}

.container-main-about-me .animate__animated {
  animation-duration: 2.5s !important;
  --animate-delay: 3s;
}

.header-main-container {
  overflow-x: hidden;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.8rem 4rem;
  position: fixed;
  z-index: 999;
  color: rgb(208, 206, 206);
  height: 1rem;
  width: 100%;
  transition: 1s ease-in-out;
  font-family: "Mulish", sans-serif !important;
}

.scrolled {
  background-color: white;
  color: #38454a;
  box-shadow: rgba(17, 17, 26, 0.1) 8px 8px 16px;
}

.scrolled a  {
  color: #38454a;

}

.scrolled .header-nav-item:hover {
  color: #2952b9;
  font-weight: 300;
  transition: all .1s ease-in-out;
}

.scrolled .icon-hamburger {
  color: black;
}

.right-content {
  margin: 0 5rem;
}

.header-logo {
  height: 3.2rem;
  width: 3.2rem;
  border-radius: 50%;
}

.header-nav-item {
  margin: 0 1.4rem;
  font-size: 1.2rem;
  font-weight: 400;
  text-decoration: none;
  color: #b1b4ba;
}

.header-nav-item:hover {
  color: white;
  font-weight: 300;
  transition: all .1s ease-in-out;
}

.iconHeader {
  font-size: 1.4rem;
  margin: -0.2rem 0.4rem;
}

.img-main {
  width: 100%;
  object-fit: cover;
  position: static;
}

.text-overlay {
  position: absolute;
  z-index: 1;
  transform: translate(-30%, -35%);
  top: 40%;
  left: 20%;
}

.text-overlay-two {
  position: absolute;
  z-index: 1;
  transform: translate(-30%, -35%);
  top: 40%;
  left: 60%;
}

.about-me-image-main {
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.textImageMain {
  color: white;
  font-size: 2.2rem;
  font-weight: 900 !important;
  font-family: "Mulish", sans-serif !important;
}

.nav-bar-tablet-phone {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: white;
  z-index: 999;
  transition: left 0.3s ease;
  height: -webkit-fill-available
}

.close-button {
  color: #414141;
  margin: 1.4rem 4rem;
  right: 0;
  position: fixed;
  font-size: 2.6rem;
}

.header-hamburger-container {
  display: none;
}

.icon-hamburger {
  font-size: 2.4rem;
  color: rgb(255, 255, 255);
  font-weight: bold;
}

.text-menu {
  font-size: 2rem;
  color: rgb(0, 0, 0);
  margin: 1.4rem 4rem;
}

.items-inside-nav-bar {
  color: black;
  list-style-type: none;
  margin-top: 4rem;
  justify-content: center;
  text-align: center;
  display: flex;
}

.list-nav-bar {
  list-style-type: none;
  font-size: 1.6rem;
}

.list-nav-bar li {
  padding: 0.6rem 0;
  color: #343333;
  font-weight: normal;
  font-family: "Mulish", sans-serif !important;
}

.items-inside-nav-bar .list-nav-bar a {
  text-decoration: none;
  color: #343333;
}


@media screen and (max-width: 1188px) {
  .right-content {
    display: none;
  }

  .container-main-text {
    height: 70vh;
  }

  .header-hamburger-container {
    display: block;
    position: fixed;
    right: 0;
    z-index: 1;
    color: red;
    margin: 0 4rem;
    justify-self: end;
  }

  .header-logo {
    height: 2.8rem;
    width: 2.8rem;
  }

  .text-overlay,
  .text-overlay-two {
    top: 32%;
  }

  .textImageMain {
    font-size: 1.6rem;
    font-weight: 700 !important;
  }

  .title-about-me {
    text-align: center;
    margin-top: 3rem;
  }

  .container-main-about-me {
    display: flex !important;
    align-items: center !important;
    margin: 0 !important;
  }

  .container-image-about-me {
    width: auto;
  }

  .image-about-me {
    height: 20rem !important;
    margin: 3rem 0;
  }

  .container-text-about-me {
    width: 100% !important;
    margin: 0 4rem;
    display: contents !important;
    text-align: center;
    padding: 0 !important;
    margin-top: -1rem;
  }

  .text-about-me {
    margin: 0 3rem;
  }

  .container-main-numbers-about-me {
    margin-top: 1rem;
  }


}

@media screen and (max-width: 1179px) {

  .container-main-about-me {
    flex-direction: column !important;
    width: 100% !important;
    align-items: baseline;
  }

  .container-text-about-me {
    display: flex;
    flex-direction: column;
  }

  .image-about-me {
    height: auto;
  }

  .container-overlay {
    display: flex;
    flex-direction: column;
  }

  .container-main-about-me {
    transition: none !important; 
  }
  
  .container-main-about-me.small {
    transform: none !important; 
    opacity: 1;
  }


}



@media screen and (max-width: 480px) {

  .text-overlay {
    top: 25%;
    left: 54%;
    width: 10rem;
    transform: translate(-50%, -50%);
  }

  .text-overlay-two {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .text-menu {
    font-size: 1.4rem;
    margin-left: 1.2rem;
  }

  .close-button {
    margin-right: 1.2rem;
    font-size: 2.1rem;
  }

  .header-hamburger-container {
    margin-right: 1rem;
  }

  .left-content {
    position: fixed;
    left: 1rem;
  }

  .list-nav-bar {
    font-size: 1.1rem;
    padding: 0;
  }

  .icon-hamburger {
    font-size: 2rem;
  }

  .header-logo {
    height: 2.2rem;
    width: 2.2rem;
  }

  .btn-main-curriculum {
    width: 8rem;
    height: 2rem;
    display: none;
  }

  .textImageMain {
    font-size: 1.6rem;
  }

  .img-main {
    height: 40%;
  }

  .header-container {
    height: 0;
    align-items: center;
  }

  .image-about-me {
    display: none;
    height: 10.5rem;
    box-shadow: 20px 20px 0px 4px #e1dfdf;
  }

  .title-about-me {
    font-size: 1.8rem !important;
    margin-top: 2rem;
  }

  .text-about-me {
    margin: 0 1rem;
    font-size: 1.1rem !important;
  }

  .container-main-numbers-about-me h1 {
    font-size: 1.6rem !important;
  }


}


@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&display=swap');

/* Estilos para la sección "Sobre mí" */
.container-main-about-me {
  display: flex;
  flex-direction: row;
  margin: 5rem 4rem;
  font-family: "Mulish", sans-serif;
}

.image-about-me {
  height: 22rem;
  object-fit: contain;
  border-radius: 4%;
  box-shadow: 40px 40px 0px 4px #e1dfdf;
}

.container-text-about-me {
  width: 50%;
  margin: 0 2rem;
  padding-left: 5rem;
  margin-top: -1rem;
}

.title-about-me {
  font-size: 2.4rem;
  color: rgb(10, 24, 33);
  line-height: 60px;
  font-weight: 900;
}

.text-about-me {
  font-size: 1.3rem;
  color: #343333;
  font-weight: 400;
  line-height: 30px;
}

.container-main-numbers-about-me {
  flex-direction: row;
  display: flex;
}

.container-main-numbers-about-me h1 {
  padding: 1rem 1.4rem;
  margin-top: -1rem;
  font-size: 2rem;
  font-weight: 700;
  color: rgb(56, 69, 74);
}

.container-main-numbers-about-me span {
  padding: 1.8rem;
  margin-top: -4rem;
  font-size: 1.2rem;
  font-weight: 700;
  color: rgb(56, 69, 74);
}

.container-numbers {
  display: flex;
  flex-direction: column;
  align-items: center;
}



button {
  border: none;
  display: flex;
  padding: 0.75rem 1.5rem;
  background-color: rgba(52, 223, 52, 0.724);
  color: #ffffff;
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 700;
  cursor: pointer;
  text-align: center;
  text-transform: uppercase;
  vertical-align: middle;
  align-items: center;
  border-radius: 0.5rem;
  user-select: none;
  gap: 0.75rem;
  box-shadow: 0 4px 6px -1px #488aec31, 0 2px 4px -1px #488aec17;
  transition: all 0.6s ease;
}


button svg {
  width: 1.25rem;
  height: 1.25rem;
}





.btn-whatApp-floating {
  position: fixed;
  z-index: 9999999;
  right: 15px;
  bottom: 1.5rem;
  background-color: rgb(37, 211, 102);
  color: rgb(255, 255, 255);
  border-radius: 10px;
  line-height: 1.6;
  height: 3rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding-right: 0 !important;
}
















