
.container-image-skills {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-container {
  width: 56%;
  overflow: hidden;
}

.ul-skills {
  display: flex;
  padding: 0;
  margin: 2rem;
}

.ul-skills li {
  padding: 0 1rem;
}

.image-skills {
  height: 3.7rem;
  width: 3.7rem;
}

.container-image-skills {
  margin-bottom: 4rem;
  align-items: center;
  width: 100%;
}

.container-image-skills ul {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  justify-content: center;
  padding: 0;
}

.container-image-skills ul li {
  padding: 0 1rem;
  display: flex;
  align-items: center;
}

.name-skills {
  font-size: 1.5rem;
  color: #ffffff;
  opacity: 1;
  line-height: 30px;
  padding-left: 0.8rem;

}

.title-skills {
  color: rgb(10, 24, 33);
  font-size: 2.2rem;
  font-weight: 900;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  text-align: center;
}

.animate__animated.animate__slideOutLeft {
  --animate-duration: 20s;
  animation-iteration-count: infinite;
}

.animate__animated.animate__slideOutRight {
  --animate-duration: 20s;
  animation-iteration-count: infinite;
}


@media screen and (max-width: 768px) {

  .animate__animated.animate__slideOutLeft {
    --animate-duration: 4s !important;
    animation-iteration-count: infinite;
  }

  .animate__animated.animate__slideOutRight {
    --animate-duration: 4s !important;
    animation-iteration-count: infinite;
  }


}


@media screen and (max-width : 480px) {
  .container-main-habilidades {
    margin-top: -6.8rem;
  }

  .title-skills {
    font-size: 1.8rem;
    margin-left: 2rem;
    margin-right: 2rem;
  }


}


@media screen and (max-width: 1179px) {

  .container-main-habilidades {
    transition: none !important;
  }

  .container-main-habilidades.small {
    transform: none !important;
    opacity: 1;
  }

  .image-container {
    width: 80% !important;
  }

}


.container-main-habilidades {
  transition: transform 0.5s ease-in-out;
}

.container-main-habilidades.small {
  transform: scale(0.8);
  opacity: 0.6;
}