.footer-main-container {
  height: 19rem;
  /* width: 100%; */
  display: flex;
  flex-direction: row;
  background-image: linear-gradient(180deg, #072348 0%, #224DB7 100%);
  color: white;
  column-gap: 8rem;
  padding: 3rem 5rem;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.1) -8px -8px 16px;
  
}

.footer-title {
  color: white;
  font-size: 1.8rem;
  /* font-size: 1.8rem;
  font-weight: 700; */
  padding-bottom: 1rem;
  display: flex;
  /* justify-content: center;
  text-align: center; */
}

.footer-text {
  font-size: 1.15rem;
  font-weight: 400;
  line-height: 30px;
}

.footer-left-container {
  width: 40%;
  /* padding-left: 4rem; */
}

.footer-right-container {
  width: 60%;
  margin-left: -3rem;
  padding-top: 3%;
}





/*Prueba*/


.footer-row {
  display: flex;
  flex-direction: row;
  margin-bottom: 2rem;
  column-gap: 2rem;
}



.input-wrapper {
  position: relative;
  font-size: 20px;
  width: -webkit-fill-available;
  /* font-weight: 900; */
}

.input-wrapper input {
  all: unset;
  width: 100%;
  padding: 4px 0;
  /* font-weight: 900; */
  border-bottom: 2px solid #dedede;
}


.input-wrapper label { 
  position: absolute;
  bottom: 10px;
  font-size: 1rem;
  left: 0;
  color: #f0f1f2;
  pointer-events: none;
  font-weight: 500;
  transition: all 0.3s ease ;
}

.input-wrapper > input:is(:focus, :valid) ~ label {
  bottom: 100%;
  font-size: 16px;
  color: #919191;
}

.input-wrapper > .underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 20%;
  height: 2px;
  background-color: #2196f3;
  scale: 0 1;
  transition:  0.3s;
}

/* .input-wrapper > input:focus ~ .underline {
  scale: 0 1;
} */

.btn-footer {
  border-radius: 0.5rem;
  font-weight: 700;
  height: 2.5rem;
  width: 7rem;
  padding: 1rem;
  margin-bottom: 3rem;
}

.footer-line {
  height: 18rem;
  width: 0.1rem;
  background-color: white;
}


@media screen and (max-width: 1179px) {
  .footer-main-container { 
    flex-direction: column;
    /* width: 100%; */
    height: auto !important;
  }

  .footer-left-container {
    align-self: center;
    width: 90%;
    margin: 0 4rem;
  }

  .footer-right-container {
    align-self: center;
    width: 90%;
    margin: 2rem 0 0 0rem;
  }

  .footer-line {
    display: none;
  }

 }


 @media screen and (max-width : 480px) {
  .footer-line {
    display: none;
  }

  /* .footer-right-container {
    margin-top: 0;
  } */

  .footer-main-container {
    padding: 1rem;
  }




 }




