.title-projects {
  font-size: 2.4rem;
  color: rgb(10, 24, 33);
  line-height: 60px;
  font-weight: 900;
  display: flex;
  justify-content: center;
}

.cards-container-projects-main {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Mulish", sans-serif;
  margin: 0 4rem !important;
}

.card-container-projects {
  height: 35rem;
  background-color: #f5f5f5e8;
  border-radius: 4%;
  margin: 3rem 1.2rem 1.2rem 1rem;
}


.card-projects-top {
  display: flex;
  justify-content: center;
}

.card-projects-image {
  padding: 1.5rem 0rem;
  height: 13rem;
  object-fit: scale-down;
  width: auto;
}

.card-container-buttons-proyects {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 2rem;
}

.card-title-projects {
  justify-content: center;
  color: #343333;
  display: flex;
  font-size: 1.2rem;
  font-weight: 800;
  margin-top: 0;
}

.buttons-projects {
  margin: 0rem 0.5rem;
  min-width: 8rem;
  height: 2rem;
  border-radius: 6rem;
  border: 1px solid rgb(176, 174, 174);
  background-color: transparent;
  padding: 0.2rem;
  justify-content: center !important;
  color: #343333 !important;
  font-weight: 500 !important;
  pointer-events: none;
}

.icons-projects {
  height: 1.6rem;
  margin-right: -0.5rem;
}

.card-projects-bottom {
  padding: 0 1.4rem;
}

.card-projects-bottom p {
  color: #343333;
  text-align: center;
  font-weight: 400;
  line-height: 30px;
  font-size: 1.1rem;
}


@media screen and (max-width: 1179px) {

  .cards-container-projects-main {
    flex-direction: column;
    margin: 0 8rem !important;
  }

  .section-projects {
    transition: none !important;
  }

  .section-projects.small {
    transform: none !important;
    opacity: 1 !important;
  }

}


@media screen and (max-width: 480px) {

  .title-projects {
    font-size: 1.8rem;
    margin-top: 2rem;
  }

  .cards-container-projects-main {
    margin-top: 0rem !important;
  }

  .card-container-projects {
    margin: 1rem 2rem !important;
  }

}


.section-projects {
  transition: transform 0.5s ease-in-out;
}

.section-projects.small {
  transform: scale(0.8);
  opacity: 0.6;
}






.title-proyects-view-images{
  display: none;
}

.card-projects-top:hover  {
  /* opacity: 0.5; */
  scale: 1.1;
  /* display: block; */
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.yarl__container {
background: rgba(0,0,0,.8) !important;
}
